import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router'
import withTranslation from 'Shared/hoc/withTranslations'
import successIcon from '../../../assets/images/icons/thanks.svg'
import './PricingSuccessPage.scss'
import appRegistry from '../../../services/AppRegistry'
import { MODAL_DATA, PAYMENT_STATUS_TYPE } from '../../CareerPro/CareerProData'

const PricingSuccessPage = (props) => {
  const UrlSearch = new URLSearchParams(props.history.location.search)
  const paymentStatus = UrlSearch.get('redirect_status')
  //   const hasToken = appRegistry.storage.get('olive-app-token')

  //   const [modalIsOpen, setModalIsOpen] = useState(false)
  //   const closeModal = () => {
  //     props.history.replace({ search: '' })
  //     setModalIsOpen(false)
  //   }

  useEffect(() => {
    // PAYMENT_STATUS_TYPE[paymentStatus]
  }, [UrlSearch])

  return (
    <div>
      {/* {paymentStatus === 'succeeded' ? 'Payment Success.....' : paymentStatus === 'fail' ? 'Payment Fail.....' : 'Somthing went wrong.....'} */}

      <div className='pricing_page_success'>
        <img src={successIcon} />
        <h3>Payment is successful!</h3>
        <p>Thank you for your payment. Your account will be automatically activated. For any clarifications feel free to reach us <a href='mailto:team@oliv.com'>team@oliv.com</a></p>
        <button>Continue</button>
      </div>
    </div>
  )
}

export default withRouter(withTranslation(PricingSuccessPage))



